@import url('https://fonts.googleapis.com/css2?family=FAMILY_NAME:wght@WEIGHT_OR_RANGE&display=swap');

body{
  margin: 0;
  font-family: "Roboto",sans-serif;
  -webkit-font-smoothing:"Roboto",sans-serif;
  -moz-osx-font-smoothing:"Roboto",sans-serif;
  font-style:normal;
}
h1,h2,h3,h4,h5,h6,p{
  margin: 0;

}

:root{
  --primary:#5e3bee;
  --heading-color:#282938;
  --bg-shade: #ebf9ff;
  --github:#e62872;
  --darkblue:#1c1e53;
  --black:#000000;
  --white:#ffffff;

}

.btn{
  font-family: "Roboto",sans-serif;
  display: inline-block;
  padding: 14px 32px;
  font-size:16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  
  -webkit-border-radius:5px;
  -moz-border-radius:5px;
  -ms-border-radius:5px;
  -o-border-radius:5px;
  border-radius: 8px;
  
  -webkit-transition:background-color 0.5s;
  -moz-transition:background-color 0.5s;
  -ms-transition:background-color 0.5s;
  -o-transition:background-color 0.5s;
  transition: background-color 0.5s;
}
.btn-outline-primary{
  color:var(--primary);
  background-color:var(--white);
  border:1px solid var(--primary);
}
.btn-outline-primary:hover{
  color:var(--white);
  background-color:var(--primary);
}
.btn-primary{
  color:var(--white);
  background-color:var(--primary);
  border:1px solid var(--primary);
}
.btn-primary:hover{
  color:var(--primary);
  background-color:var(--white);
}
.btn-github{
  color:var(--white);
  background-color:var(--github);
  border:var(--github);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40,0.95);
  gap: 16px;
}
.btn-github:hover{
  color:#000000;
  background-color:var(--white);
}


/*main*/
h1{
  font-size: 56px;
  font-weight: 700;
  line-height: 67px;
}
h2{
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
}
h3{
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

/* body*/
.text-lg{
  color: var(--darkblue);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
}

.text-md{
  color: var(--darkblue);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;

}

.text-sm{
  color: var(--black);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}


/* title section */


/* skills and about */

.section-title{
  color: var(--heading-color);
  font-size: 21px;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  display: flex;
  align-items: flex-start;
}


/* portfolio */
.sub-title{
  color: var(--heading-color);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}


/* navbar */
.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 85.333px;
  background: var(--white);
  box-shadow: 0px 5.333px 18px 0 rgba(0, 0,0 ,0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.navbar-items > ul{
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
  padding:0 120px 0 0;
}

.navbar-items ul>li>a{
  text-decoration: none;
}

/* navbar content */

.navbar-content{
  color: var(--darkblue);
  font-size:16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
}

.navbar-active-content{
  color: var(--primary);
}

/* hero section */
.hero-section{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  padding: 133.333px 8.333px 133.333px 85.33px;
  align-items: center;
  justify-content: space-between;
  gap: 32px;
  background-color: var(--bg-shade);
}

.hero-section-content-box{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
}

.hero-section-content{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.333px;
}

.hero-section-content-box > button{
  margin-top: 21.333px;
}

.hero-section-title{
  color: var(--heading-color);
  font-size: 74.667px;
  font-weight: 700;
  line-height: 90px;
  align-self: stretch;
}

.hero-section-title-color{
  color: var(--primary);
}

.hero-section-description{
  color: var(--darkblue);
  font-size: 24px;
  font-weight: 400;
  line-height: 36px; 
  text-align: justify ;
}
.hero-section-img{
  display: flex;
}

.hero-section-img > img{
  height: 30%;
  width: 80%;
  
  border-radius: 30px;
}

/* skills */
.skills-section{
  display: flex;
  padding: 149.33px 85.33px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 106.667px;
}

.skills-section-heading{
  color: var(--heading-color);
  font-size: 64px;
  font-weight: 700;
  line-height: 77px;
}

.skills-section-container{
  display: grid;
  justify-content: center ;
  align-items:flex-start;
  gap: 42.6px;
  grid-template-columns: repeat(4,1fr);
}

.skills-section-card{
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items:flex-start;
  gap: 32px;
  flex: 1 0 0;
  border-radius: 10.6px;
  background: var(--bg-shade);
  min-height: 250px;
}

.skills-section-card:hover{
  border-bottom: 4px solid var(--primary);
}

.skills-section-card:hover .skills-section-decription{
  color: var(--darkblue);
}

.skills-section-img > img{
  width: 30px;
  height: 30px;
}
.skills-section-img{
  display: flex;
  padding: 13.3px;
  justify-content: center;
  align-items: center;
  gap: 13.3px;
  border-radius: 46.6px;
  background: #ffff;
  box-shadow: 0 0 16px 0 rgba(0,0,0,0.1);

}

.skills-section-card-content{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;

}

.skills-section-title{
  color: var(--heading-color);
  font-size: 32px ;
  font-weight: 700;
  line-height: 45px;
}

.skills-section-description{
  color: var(--black);
  font-size:21.3px ;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  
}

/* About section */
.about-section{
  display: grid;
  padding: 133.3px 85.3px;
  align-items: 114.6px;
  grid-template-columns: repeat(2,1fr);
}

.about-section-img > img{
  width: 100%;
  height: 100%;
}

/* portfolio */
.portfolio-section{
  display: flex;
  padding: 112px 85px;
  flex-direction: column;
  align-items: flex-start;
  gap: 80px;
}

.portfolio-container-box{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.portfolio-container{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.3px;
}

.portfolio-section-container{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 21.3px;
  width: 100%;
}

.portfolio-section-img{
  border-radius: 8px;
  width: 100%;
}

.portfolio-section-img > img{
  border-radius: 8px;
  margin-left: 0.5px;
  width: 320px;
  height: 300px;
}

.portfolio-section-card{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 600px;
  gap: 32px;
  flex: 1 0 0 0;
  background: #fff;
  box-shadow: 0 0 32px 0 rgba(0, 0,0 ,0.15);
  cursor: pointer;
  border-top-right-radius: 12px;
  border-top-left-radius: 12px;
}
.portfolio-section-card-content{
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;
}

.portfolio-section-title{
  color: var(--heading-color);
}

.portfolio-link{
  text-decoration: none;
  display: flex;
  gap: 16px;
  border-bottom: 1.33px solid var(--primary);
  padding: 10.6px;
  font-weight: 600;


}

.portfolio-section-card:hover path{
  stroke: #006b6a;
}

/* testimonial */
.testimonial-section{
  display: flex;
  padding: 112px 84.3px;
  flex-direction: column;
  align-items: flex-start;
  gap: 80px;
  background-color: var(--bg-shade);
}
.sections-heading{
  color: var(--heading-color);
}

.testimonial-section-card{
  display: flex;
  padding: 42.6px;
  flex-direction: column;
  align-items: flex-start;
  gap: 42.6px;
  flex: 1 0 0;
  border-radius: 8px;
  border: 1.33px solid #006b6a;
}

.testimonial-section-card-review{
  display: flex;
  align-items: flex-start;
  gap: 5.33px;
}

.testimonial-section-card-author-detail{
  display: flex;
  align-items: center;
  gap: 21.3px;
}
.testimonial-section-card-author-detail >img{
  width: 150px;  
  height: 200px;
}

.testimonial-author-name{
  font-weight: 600;
  color: var(--heading-color);

}

.testimonial-author-design{
  color: var(--darkblue);
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.contact-section{
  display: flex;
  padding: 150px 85px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 64px;
}

.contact-form-container{
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(5,auto);
  width: 40%;
  row-gap: 32px;
}

.container{
  display: grid;
  grid-template-columns: repeat(1,auto);
  column-gap: 32px;
  row-gap: 32px;
}

.contact-label{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10.6px;
  flex: 1 0 0;
  width: 100%;
}

.contact-input{
  display: flex;
  font-family: "Roboto";
  padding: 16px;
  align-items: center;
  align-self: stretch;
  border-radius: 8px;
  border: 1.333px solid var(--primary);
  background: var(--white);
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="32" height="33" viewBox="0 0 32 33" fill="none"><path fill-rule="evenodd" clip-rule="evenodd" d="M16.5303 21.2052C16.2374 21.4981 15.7626 21.4981 15.4697 21.2052L7.82318 13.5587C7.53029 13.2658 7.53029 12.791 7.82318 12.4981L8.17674 12.1445C8.46963 11.8516 8.9445 11.8516 9.2374 12.1445L16 18.9071L22.7626 12.1445C23.0555 11.8516 23.5303 11.8516 23.8232 12.1445L24.1768 12.4981C24.4697 12.791 24.4697 13.2658 24.1768 13.5587L16.5303 21.2052Z" fill="%23033333"/></svg>')
    white no-repeat 98.5% !important; /* !important used for overriding all other customisations */
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="32" height="33" viewBox="0 0 32 33" fill="none"><path fill-rule="evenodd" clip-rule="evenodd" d="M16.5303 21.2052C16.2374 21.4981 15.7626 21.4981 15.4697 21.2052L7.82318 13.5587C7.53029 13.2658 7.53029 12.791 7.82318 12.4981L8.17674 12.1445C8.46963 11.8516 8.9445 11.8516 9.2374 12.1445L16 18.9071L22.7626 12.1445C23.0555 11.8516 23.5303 11.8516 23.8232 12.1445L24.1768 12.4981C24.4697 12.791 24.4697 13.2658 24.1768 13.5587L16.5303 21.2052Z" fill="%23033333"/></svg>')
    white no-repeat calc(100% - 20px); /* Better placement regardless of input width */
}

select::-ms-expand {
  display: none;
}

.checkbox-label{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10.6px;
  flex: 1 0 0;
  justify-content: flex-start;
}

input[type="checkbox"]{
  width: 21px;
  height: 21px;
  border: 0.15em solid var(--heading-color);
  border-radius: 0.15em;
  transform: translateY(-0.075em);
}

/* footer */

.footer-container {
  display: flex;
  padding: 106.667px 85.333px;
  flex-direction: column;
  align-items: flex-start;
  background: var(--bg-shade);
  align-items: center;
  align-self: stretch;
}
.footer-link-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}
.footer-items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
}
.footer-items ul > li > a {
  text-decoration: none;
}
.footer-social-icon > ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 16px;
  flex-shrink: 0;
  text-decoration: none;
}
.divider {
  margin: 106px 0 42.67px;
  height: 1.333px;
  width: 100%;
  background: rgba(40, 41, 56, 0.55);
}
.footer-content-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}
.footer-content {
  color: var(--black);
  font-size: 18.667px;
  font-weight: 400;
  line-height: 28px;
}

.footer-link-container-img > img{
  padding-left: 40px;
  width: 70px;
  height: 70px;
}

