    background-color: var(--bg-shade);
  }
  

  .hero-section-img > img{
    height: 400px;
    width: 400px; 
    border-radius: 30px;
  }

  .skills-section{
    display: flex;
    padding: 149.33px 85.33px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
