    margin-left: 100px;
  }
  
  .skills-section-heading{
    color: var(--heading-color);
    font-size: 64px;
    font-weight: 700;
    line-height: 77px;
    
  }
  
  .skills-section-container{
    display: flex;
    justify-content: center ;
    flex-direction: column;
    align-items:flex-start;
    gap: 42.6px;
    margin-left: 90px;
  }
  
  .skills-section-card{
    display: flex;
    width: 300px;
    flex-direction: column;
    align-items:flex-start;
