    padding: 112px 85px;
    flex-direction: column;
    align-items: flex-start;
    gap: 80px;
    
  }
  
  .portfolio-container-box{
    display: grid;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    
  }
  
  .portfolio-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 21.3px;
   
  }
  
  .portfolio-section-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  }
  
  .portfolio-section-img > img{
